import React, { useState, useContext } from 'react';
import { useNavigate } from "react-router-dom";
import { useEffect } from 'react';

import axios from "axios";

import SimpleWhite from '../Assets/img/SimpleWhite';
import ProfileIcon from '../Assets/img/ProfileIcon';
import ArrowRight from '../Assets/img/ArrowRight';
import Plus from '../Assets/img/Plus';

import UserContext from "../Store/user-context";

const Profile = () => {
    const [selectedColor, setSelectedColor] = useState('red');
    const [isFocusedName, setIsFocusedName] = useState(false);
    const [isFocusedPass, setIsFocusedPass] = useState(false);
    const [name, setName] = useState();
    const [oldName, setOldName] = useState();
    const [pass, setPass] = useState();
    const [passRepeat, setPassRepeat] = useState();
    const [formSelected, setFormSelected] = useState(false);
    const [alert, setAlert] = useState({status: false, response: '-'});

    const Joi = require("joi");
    const userCtx = useContext(UserContext);
    const navigate = useNavigate();

    useEffect(()=>{
        setSelectedColor(userCtx.color);
    }, [])
    
    
    const changeColor = (color) => {

        const value = {
            user: userCtx.userName,
            color
        }
        userCtx.chUserColor(color); 
        setSelectedColor(color);
        /* change color axios */
        axios.post(`${process.env.REACT_APP_API_URL}/changecolor`, value)
    };

    const changeName = () => {
        const Schema = Joi.object({
            newUser: Joi.string().min(3).max(30).required(),
        });

        const { error, value } = Schema.validate({
            newUser: name,
        });
        value.user = userCtx.userName;

        if (error) {
            if (error.toString().includes('length')) {
                setAlert({status: 'error', response: 'el nombre debe tener entre 3 y 30 caracteres'});
            }else{
                setAlert({status: 'error', response: 'algo salió mal :('});
            }
        } else {
            if (userCtx.userName === oldName) {
                userCtx.chUserName(name)
                axios.post(`${process.env.REACT_APP_API_URL}/changename`, value)
                setAlert({status: 'success', response: 'El nombre se cambió correctamente'});
                setTimeout(() => {
                    setAlert({status: false, response: '-'});
                    changeForm(false);
                }, 5000);
            }else{
                setAlert({status: 'error', response: 'algo salió mal :('});
            }
        }
        
    };

    const changePass = () => {

        const Schema = Joi.object({
            pass: Joi.string().pattern(new RegExp("^[a-zA-Z0-9]{3,30}$")),
    
            repeat_password: Joi.ref("pass"),
        });

        const { error, value } = Schema.validate({
            pass: pass,
            repeat_password: passRepeat
        }, { abortEarly: false });

        value.user = userCtx.userName;
        if (error) {
            console.table(error);
            if (error==='ValidationError: "repeat_password" must be [ref:pass]') {
                setAlert({status: 'error', response: 'Las contraseñas no coinciden'});
            }else{
                setAlert({status: 'error', response: 'La contraseña debe tener entre 3 y 30 caracteres y solo puede contener letras y numeros [a-z][0-9][A-Z]'});
            }
        } else {
            axios.post(`${process.env.REACT_APP_API_URL}/changepass`, value).then((result) => {
                setAlert({status: 'success', response: 'La contraseña se cambió correctamente'});
                
            })
            

            setTimeout(() => {
                setAlert({status: false, response: '-'});
                changeForm(false);
                navigate('/', { replace: true });
                userCtx.deConUser();
            }, 5000);
        }
    };

    const changeForm = (formToDisplay) => {
        setAlert({status: false, response: '-'});
        setFormSelected(formToDisplay);
    };

    return (
        <div className='profile'>
            <div className='title'>Hola <span className={userCtx.color}>{userCtx.userName}</span>!</div>
            <div className='options'>
                <div className='colorSelector'>
                    <div className='colorLabel'>Elegí tu color <SimpleWhite /></div>
                    <div className={'colors ' + selectedColor}>
                        <div className='purple' onClick={() => {changeColor('purple')}}><ProfileIcon color='fff'/></div>
                        <div className='red' onClick={() => {changeColor('red')}}><ProfileIcon color='fff'/></div>
                        <div className='orange' onClick={() => {changeColor('orange')}}><ProfileIcon color='fff'/></div>
                        <div className='green' onClick={() => {changeColor('green')}}><ProfileIcon color='fff'/></div>
                        <div className='sky' onClick={() => {changeColor('sky')}}><ProfileIcon color='fff'/></div>
                        <div className='violet' onClick={() => {changeColor('violet')}}><ProfileIcon color='fff'/></div>
                    </div>
                </div>
                <div className={'nameChanger ' + userCtx.color}>
                    <div className='nameLabel'>cambiá tu nombre</div>
                    <div className='name'>
                        <label className={isFocusedName ? 'placeholder focused' : 'placeholder'} >nuevo nombre</label>
                        <input readonly onChange={(e) => { setName(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedName(true) : setIsFocusedName(false) }} onFocus={() => {setIsFocusedName(true); this.removeAttribute('readonly'); }} type="text" />
                        <div className='btnSq' onClick={() => {changeForm('chName')}}><ArrowRight /></div>
                    </div>
                </div>
                <div className={'passChanger ' + userCtx.color}>
                    <div className='passLabel'>cambiá tu contraseña</div>
                    <div className='password'>
                        <label className={isFocusedPass ? 'placeholder focused' : 'placeholder'} >nueva contraseña</label>
                        <input readonly onChange={(e) => { setPass(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedPass(true) : setIsFocusedPass(false) }} onFocus={() => {setIsFocusedPass(true); this.removeAttribute('readonly'); }} type="text" />
                        <div className='btnSq' onClick={() => {changeForm('chPass')}}><ArrowRight /></div>
                    </div>
                </div>
            </div>
            <div className='table'></div>
            { formSelected==='chName' ? 
            <div className={'popup visible ' + userCtx.color}>
                <div className='overflow'></div>
                <div className='closeBtn' onClick={()=>{changeForm(false)}}><Plus/></div>
                <div className='form'>
                    <form onSubmit={(e)=>{e.preventDefault()}}>
                        <div className='title'>escribe tu nombre de usuario para confirmar el cambio<span>{userCtx.userName}</span></div>
                        <div className='name'>
                            <label className={isFocusedName ? 'placeholder focused' : 'placeholder'} >tu nombre</label>
                            <input readonly onChange={(e) => { setOldName(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedName(true) : setIsFocusedName(false) }} onFocus={() => {setIsFocusedName(true); this.removeAttribute('readonly'); }} type="text" />
                            <div className='btnSq' onClick={() => {changeName()}}><ArrowRight /></div>
                        </div>
                        <div className='help'>
                            <div className='alertBox'>!</div>
                            <div className='alertMsg'>luego de enviar el formulario el nombre de usuario será cambiado</div>
                        </div>
                    </form>
                    <div className={alert.status ? alert.status==='success' ? 'response visible success' : 'response visible error' : 'response'}>{alert.response}</div>
                </div>
            </div>
            : formSelected==='chPass' ?
            <div className={'popup visible ' + userCtx.color}>
                <div className='overflow'></div>
                <div className='closeBtn' onClick={()=>{changeForm(false)}}><Plus/></div>
                <div className='form'>
                    <form onSubmit={(e)=>{e.preventDefault()}}>
                        <div className='title'>confirma tu contraseña</div>
                        <div className='name'>
                            <label className={isFocusedPass ? 'placeholder focused' : 'placeholder'} >repetir contraseña</label>
                            <input readonly onChange={(e) => { setPassRepeat(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedPass(true) : setIsFocusedPass(false) }} onFocus={() => {setIsFocusedPass(true); }} type="text" />
                            <div className='btnSq' onClick={() => {changePass()}}><ArrowRight /></div>
                        </div>
                        <div className='help'>
                            <div className='alertBox'>!</div>
                            <div className='alertMsg'>luego de enviar el formulario la contraseña será cambiada</div>
                        </div>
                    </form>
                    <div className={alert.status ? alert.status==='success' ? 'response visible success' : 'response visible error' : 'response'}>{alert.response}</div>
                </div>
            </div> 
            : null}
        </div>
    )
}

export default Profile