import React, { useState, useContext } from 'react'
import { useNavigate } from "react-router-dom";

import axios from "axios";
import Swal from 'sweetalert2';

import Plus from '../Assets/img/Plus';
import ProfileIcon from '../Assets/img/ProfileIcon';
import SimpleWhite from '../Assets/img/SimpleWhite';

import UserContext from "../Store/user-context";

const Login = (params) => {
    /*useState*/
    const [isFocusedName, setIsFocusedName] = useState(false);
    const [isFocusedMail, setIsFocusedMail] = useState(false);
    const [isFocusedPassword, setIsFocusedPassword] = useState(false);
    const [isFocusedPasswordRepeat, setIsFocusedPasswordRepeat] = useState(false);
    const [nameError, setNameError] = useState(false);
    const [mailError, setMailError] = useState(false);
    const [passError, setPassError] = useState(false);
    const [passRepeatError, setPassRepeatError] = useState(false);
    const [name, setName] = useState();
    const [mail, setMail] = useState();
    const [password, setPassword] = useState();
    const [passwordRepeat, setPasswordRepeat] = useState();
    const [selectedColor, setSelectedColor] = useState('red');

    /*plugins*/
    const Joi = require("joi");
    const navigate = useNavigate();
    const userCtx = useContext(UserContext);

    /*Switch between forms*/
    const changeForm = function(formToDisplay) {
        params.setFormSelected(formToDisplay);
        setNameError(false);
        setMailError(false);
        setPassError(false);
        setPassRepeatError(false);
        setIsFocusedName(false);
        setIsFocusedMail(false);
        setIsFocusedPassword(false);
        setIsFocusedPasswordRepeat(false);
    }

    
    

    /* Login */
    const SchemaSignin = Joi.object({
        user: Joi.string().min(3).max(30).required(),

        pass: Joi.string().required().pattern(new RegExp("^[a-zA-Z0-9]{3,30}$")),
    });
    const loginHandler = (e) => {
        
        e.preventDefault();
        const { error, value } = SchemaSignin.validate({
            user: name,
            pass: password,
        }, { abortEarly: false });

        setNameError(false);
        setMailError(false);
        setPassError(false);
        setPassRepeatError(false);

        
        if (error) {
            
            const errors = error.message.split('.');
            for (let index = 0; index < errors.length; index++) {
                const err = errors[index];
                if (err.includes("user")) {
                    setNameError(err)
                }
                if(err.includes("pass")){
                    error.message.includes("[a-zA-Z0-9]{3,30}") ? setPassError("The password must be 3 to 30 characters long") : setPassError(err);
                }
            }
            
        } else {
            axios
                .post(`${process.env.REACT_APP_API_URL}/login`, value)
                .then(function (result) {
                    if (result.data === "Username entered was incorrect") {
                        setNameError("Username entered was incorrect");
                    } else if (result.data === "Account disabled") {
                        setNameError("Account disabled");
                    } else if (result.data === "Password entered was incorrect") {
                        setPassError("Password entered was incorrect");
                    } else {

                        userCtx.conUser(
                            result.data.userId,
                            result.data.userName,
                            result.data.color,
                            result.data.hexacolor
                        );

                        
                        
                        localStorage.username = name;
                        localStorage.password = password;
                        localStorage.userToken = result.data.userToken;

                        changeForm(false);

                    }
                });
        }
    };

    
    const SchemaSignUp = Joi.object({
        user: Joi.string().min(3).max(30).required(),

        mail: Joi.string().email({
            minDomainSegments: 2,
            tlds: { allow: ["com", "net"] },
        }),

        pass: Joi.string().pattern(new RegExp("^[a-zA-Z0-9]{3,30}$")),

        repeat_password: Joi.ref("pass"),
    });

    const registerHandler = (e) => {
        e.preventDefault();

        
        setNameError(false);
        setMailError(false);
        setPassError(false);
        setPassRepeatError(false);
        
        const { error, value } = SchemaSignUp.validate({
            user: name,
            mail: mail,
            pass: password,
            repeat_password: passwordRepeat
        }, { abortEarly: false });

        if (error) {
            console.log(error.message)
            const errors = error.message.split('.');
            for (let index = 0; index < errors.length; index++) {
                const err = errors[index];
                if(err.includes("user")){
                    setNameError(err);
                }
                if (err.includes("mail")) {
                    setMailError(err);
                }
                if(err.includes('"pass"')){
                    if (password.length > 30 || password.length < 3) {
                        setPassError("Password must contain 3 to 30 characters");
                    } else {
                        error.message.includes('match the required pattern: /^[a-zA-Z0-9]')
                            ? setPassError("Password must contain characters with the pattern (a-z, A-Z and 0-9) and 3 to 30 characters long") 
                            : setPassError(err);
                    }
                    
                }
                if(err.includes("repeat_password")){
                    setPassRepeatError("Passwords do not match")
                }
            }
        } else {
            
            value.color = selectedColor;
            const userDataReg = value;

            axios
                .post(`${process.env.REACT_APP_API_URL}/register`, userDataReg)
                .then((result) => {
                    
                    if (result.data === "Name is already registered") {
                        
                        setNameError("Username is already registered");
                    } else if(result.data === "Email is already registered") {
                        setMailError("Email is already registered");
                    } else {
                        params.setFormSelected('signin');
                        /* created account succesfully popup */
                    }
                    
                });
        }
    };


    if (params.formSelected === 'signin') {
        return (
            <div className='popup signin visible'>
                        <div className='overflow'></div>
                        <div className='closeBtn' onClick={()=>{changeForm(false)}}><Plus/></div>
                        <div className='form'>
                            <form onSubmit={loginHandler}>
                                <div className='title'>Iniciar sesión</div>
                                <div className='name'>
                                    <label className={isFocusedName ? 'placeholder focused' : 'placeholder'} >tu nombre</label>
                                    <input readonly onChange={(e) => { setName(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedName(true) : setIsFocusedName(false) }} onFocus={() => {setIsFocusedName(true); this.removeAttribute('readonly'); }} type="text" />
                                    {nameError ? <div className='error'>!<div className='errormsg'>{nameError}</div></div> : null}
                                </div>
                                <div className='password'>
                                    <label className={isFocusedPassword ? 'placeholder focused' : 'placeholder'} >tu contraseña</label>
                                    <input readonly onChange={(e) => { setPassword(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedPassword(true) : setIsFocusedPassword(false) }} onFocus={() => {setIsFocusedPassword(true); this.removeAttribute('readonly'); }} type="password"/>
                                    {passError ? <div className='error'>!<div className='errormsg'>{passError}</div></div> : null}
                                    
                                </div>
                                <div className='help'>
                                    <div onClick={()=>{changeForm('forgot')}}>¿Olvidaste tu contraseña?</div>
                                    <div onClick={()=>{changeForm('signup')}}>¿No estás registrado?</div>
                                </div>
                                <div className='submit'>
                                    <input className='btn' value='iniciar sesión' type="submit"/>
                                </div>
                            </form>
                        </div>
                    </div>
        );
    }else if(params.formSelected === 'signup'){
        return(
            <div className='popup signup visible'>
                        <div className='overflow'></div>
                        <div className='closeBtn' onClick={()=>{changeForm(false)}}><Plus/></div>
                        <div className='form'>
                            <form onSubmit={registerHandler}>
                                <div className='title'>Registrarse</div>
                                <div className='name'>
                                    <label className={isFocusedName ? 'placeholder focused' : 'placeholder'} >tu nombre</label>
                                    <input autoComplete="new-password" onChange={(e) => { setName(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedName(true) : setIsFocusedName(false) }} onFocus={() => {setIsFocusedName(true)}} type="text"/>
                                    {nameError ? <div className='error'>!<div className='errormsg'>{nameError}</div></div> : null}
                                </div>
                                <div className='mail'>
                                    <label className={isFocusedMail ? 'placeholder focused' : 'placeholder'} >tu mail</label>
                                    <input autoComplete="new-password" onChange={(e) => { setMail(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedMail(true) : setIsFocusedMail(false) }} onFocus={() => {setIsFocusedMail(true)}} type="text"/>
                                    {mailError ? <div className='error'>!<div className='errormsg'>{mailError}</div></div> : null}
                                </div>
                                <div className='password'>
                                    <label className={isFocusedPassword ? 'placeholder focused' : 'placeholder'} >tu contraseña</label>
                                    <input autoComplete="new-password" onChange={(e) => { setPassword(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedPassword(true) : setIsFocusedPassword(false) }} onFocus={() => {setIsFocusedPassword(true)}} type="password"/>
                                    {passError ? <div className='error'>!<div className='errormsg'>{passError}</div></div> : null}
                                </div>
                                <div className='repeat password'>
                                    <label className={isFocusedPasswordRepeat ? 'placeholder focused' : 'placeholder'} >repetir contraseña</label>
                                    <input autoComplete="new-password" onChange={(e) => { setPasswordRepeat(e.target.value); }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedPasswordRepeat(true) : setIsFocusedPasswordRepeat(false) }} onFocus={() => {setIsFocusedPasswordRepeat(true)}} type="password"/>
                                    {passRepeatError ? <div className='error'>!<div className='errormsg'>{passRepeatError}</div></div> : null}
                                </div>
                                <div className='colorLabel'>Elegí tu color <SimpleWhite /></div>
                                <div className={'colors ' + selectedColor}>
                                    <div className='purple' onClick={() => {setSelectedColor('purple')}}><ProfileIcon color='fff'/></div>
                                    <div className='red' onClick={() => {setSelectedColor('red')}}><ProfileIcon color='fff'/></div>
                                    <div className='orange' onClick={() => {setSelectedColor('orange')}}><ProfileIcon color='fff'/></div>
                                    <div className='green' onClick={() => {setSelectedColor('green')}}><ProfileIcon color='fff'/></div>
                                    <div className='sky' onClick={() => {setSelectedColor('sky')}}><ProfileIcon color='fff'/></div>
                                    <div className='violet' onClick={() => {setSelectedColor('violet')}}><ProfileIcon color='fff'/></div>
                                </div>
                                <div className='help'>
                                    <div onClick={()=>{changeForm('signin');}}>¿Ya tenés cuenta? Iniciá sesión.</div>
                                    {passError ? <div className='error'>!<div className='errormsg'>Hola</div></div> : null}
                                </div>
                                <div className='submit'>
                                    <input className='btn' value='registrame' type="submit"/>
                                </div>
                            </form>
                        </div>
                    </div>
        );
    }else if(params.formSelected === 'forgot'){
        return(
            <div className='popup signup visible'>
                <div className='overflow'></div>
                <div className='closeBtn' onClick={()=>{changeForm(false)}}><Plus/></div>
                <div className='form'>
                    <form action="">
                        <div className='title'>Recuperar contraseña</div>
                        <div className='name'>
                            <label className={isFocusedName ? 'placeholder focused' : 'placeholder'} >tu nombre</label>
                            <input onChange={(e) => { setName(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedName(true) : setIsFocusedName(false) }} onFocus={() => {setIsFocusedName(true)}} type="text"/>
                        </div>
                        <div className='mail'>
                            <label className={isFocusedMail ? 'placeholder focused' : 'placeholder'} >tu mail</label>
                            <input onChange={(e) => { setMail(e.target.value) }} onBlur={(e)=>{ e.target.value.toString()!=='' ? setIsFocusedMail(true) : setIsFocusedMail(false) }} onFocus={() => {setIsFocusedMail(true)}} type="text"/>
                        </div>
                        <div className='submit'>
                            <input className='btn cancel' value='cancelar' onClick={() => {changeForm('signin')}}/>
                            <input className='btn' value='recuperar' type="submit"/>
                        </div>
                    </form>
                </div>
            </div>
        );
    }else{
        return(
            <div className='popup'></div>
        );
    }
}

export default Login