import React, { useState } from 'react'

import Asterisk from '../Assets/img/Asterisk';

const Contact = () => {
    const [isFocusedName, setIsFocusedName] = useState(false);
    const [isFocusedMail, setIsFocusedMail] = useState(false);
    const [isFocusedText, setIsFocusedText] = useState(false);
    const [name, setName] = useState();
    const [Mail, setMail] = useState();
    const [Text, setText] = useState();

    return (
        <div className='contact'>
            <div className='title'><Asterisk color="8FC98E" /><h3> Contactanos </h3><Asterisk color="8FC98E" /></div>
            <form action="">
                <div className='name'>
                    <label className={isFocusedName ? 'placeholder focused' : 'placeholder'} >tu nombre</label>
                    <input onChange={(e) => { setName(e.target.value); e.target.value.toString()!=='' ? setIsFocusedName(true) : setIsFocusedName(false) }} onClick={() => {setIsFocusedName(true)}} type="text"/>
                </div>
                <div className='mail'>
                    <label className={isFocusedMail ? 'placeholder focused' : 'placeholder'}>tu e-mail</label>
                    <input onChange={(e) => { setMail(e.target.value); e.target.value.toString()!=='' ? setIsFocusedMail(true) : setIsFocusedMail(false)  }} onClick={() => {setIsFocusedMail(true)}} type="text"/>
                </div>
                <div className='msg'>
                    <label className={isFocusedText ? 'placeholder focused' : 'placeholder'}>tu mensaje</label>
                    <textarea onChange={(e) => { setText(e.target.value); e.target.value.toString()!=='' ? setIsFocusedText(true) : setIsFocusedText(false)  }} onClick={() => {setIsFocusedText(true)}} type="textarea"/>
                </div>
                <div className='submit'>
                    <input className='btn' value='enviar' type="submit"/>
                </div>
            </form>

        </div>
    )
}

export default Contact