import { createContext, useState } from "react";

const UserContext = createContext({
    userId: null,
    userCon: false,
    userName: null,
    color: null,
    hexacolor: null,
    conUser: (user) => {},
    deConUser: (user) => {},
    chUserName: (user) => {},
    chUserColor: (user) => {}
});

export function UserContextProvider(props) {
    const [userId, setUserId] = useState();
    const [userName, setUserName] = useState();
    const [color, setColor] = useState();
    const [hexacolor, setHexacolor] = useState();
    const [userConnected, setUserConnected] = useState(false);
    

    function connectUserHandler(userId, userName, color, hexacolor) {
        setUserId(userId);
        setUserName(userName);
        setColor(color);
        setHexacolor(hexacolor);
        setUserConnected(true);
    }

    function deConnectUserHandler() {
        setUserId(false);
        setUserName(false);
        setUserConnected(false);
        setColor(false);
        setHexacolor(false)
    }

    function changeColor(newColor) {
        setColor(newColor);
        let hexacolor = 'F44';
        switch (newColor)
        {
            case "purple":
                hexacolor = '8787F3';
                break;
            case "red":
                hexacolor = 'F44';
                break;
            case "orange":
                hexacolor = 'FF9500';
                break;    
            case "green":
                hexacolor = '8FC98E';
                break;
            case "sky":
                hexacolor = '71DDEA';
                break;
            case "violet":
                hexacolor = '840EE3';
                break;    
        }
        setHexacolor(hexacolor);
    }

    function changeUserName(newName) {
        setUserName(newName);
    }

    const context = {
        userId: userId,
        userCon: userConnected ? true : false,
        userName: userName,
        color: color ? color : 'red',
        hexacolor: hexacolor ? hexacolor : 'F44',
        conUser: connectUserHandler,
        deConUser: deConnectUserHandler,
        chUserName: changeUserName,
        chUserColor: changeColor
    };

    return (
        <UserContext.Provider value={context}>
            {props.children}
        </UserContext.Provider>
    );
}

export default UserContext;