import React, { useState, useContext } from 'react'

import UserContext from "../Store/user-context";

const Filters = () => {
    const [filtersActive, setFiltersActive] = useState(false);

    const userCtx = useContext(UserContext);

    return (
        <div className={filtersActive ? 'filters active' : 'filters'}>
            <div className={'btn ' + userCtx.color} onClick={()=>{setFiltersActive(!filtersActive)}}>filtros</div>
            <div className='filter' style={{left: -110*1 + 'px', transitionDuration: '.5s'}}> filter1 </div>
            <div className='filter' style={{left: -110*2 + 'px', transitionDuration: '.5s'}}> filter1 </div>
            <div className='filter' style={{left: -110*3 + 'px', transitionDuration: '.5s'}}> filter1 </div>
        </div>
    )
}

export default Filters