import React from 'react'
import ColorLine1 from '../Assets/img/ColorLine1';
import TriBlack from '../Assets/img/ColorLine2';

import Herzio from '../Assets/img/Herzio';

const Main = () => {
    return (
        <div className='content'>
            <div className="logoBack">
                <Herzio />
            </div>
            <div className='text'>
                <p className='prod'>produciendo</p>
                <p className='correction'>Artistas <span className='comp'>componiendo</span> beats para vos.</p>
                <p className='create'>creando</p>
            </div>
            <div className='triBlack'>
                <TriBlack />
            </div>
            <div className='colorLine'>
                <ColorLine1 />
            </div>
            
        </div>
    )
}

export default Main