import React, { useState, useContext } from 'react'
import { useParams } from "react-router-dom";

import logo from '../Assets/img/test.jpeg';
import Synth from '../Assets/img/Synth';
import Drums from '../Assets/img/Drums';
import Fx from '../Assets/img/Fx';
import Air from '../Assets/img/Air';
import Cord from '../Assets/img/Cord';
import Other from '../Assets/img/Other';
import Star from '../Assets/img/Star';
import Heart from '../Assets/img/Heart';
import CartMark from '../Assets/img/CartMark';

import UserContext from "../Store/user-context";

const Product = () => {
    const { uid } = useParams();
    const [isFaved, setIsFaved] = useState(false);

    const userCtx = useContext(UserContext);

    return (
        <div className='product'>
            <div className='data'>
                <div className='photoWrapper'>
                    <img src={logo} alt="" />
                </div>
                <div className='info'>
                    <div className={'genre ' + userCtx.color}>Genero</div>
                    <div className='title'>pack #14</div>
                    <div className='moreInfo'>
                        <div className={'author ' + userCtx.color}>Pedro Gonzales</div>
                        <Star color={userCtx.hexacolor}/>
                        <div className='year'>2022</div>
                        <Star color={userCtx.hexacolor}/>
                        <div className='samplesAmount'>32 samples</div>
                        <Star color={userCtx.hexacolor}/>
                        <div className='time'>14 minutos 32 segundos</div>
                    </div>
                    <div className='desc'>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</div>
                    <div className='actions'>
                        <div className={'buyNow ' + userCtx.color}>Comprar ahora</div>
                        <div className='btnATC'><CartMark color1="000" color2={userCtx.hexacolor}/><span className='plus'>+</span></div>
                        <div className='btnFav' onMouseEnter={()=>{setIsFaved(true)}} onMouseLeave={()=>{setIsFaved(false)}} onClick={()=>{setIsFaved(!isFaved)}}>{isFaved ? <Heart color1={userCtx.hexacolor} color2='000'/> : <Heart color1='fff' color2='000'/>}</div>
                    </div>
                </div>
            </div>
            <div className='extraInfo'>
                <div className='instrument'>
                    <div className='photo'>
                        <Synth />
                    </div>
                    <div className='dataInst'>
                        <div className='qty'>4</div>
                        <div className='name'>synth</div>
                    </div>
                </div>
                <div className='instrument'>
                    <div className='photo'>
                        <Drums />
                    </div>
                    <div className='dataInst'>
                        <div className='qty'>4</div>
                        <div className='name'>drums</div>
                    </div>
                </div>
                <div className='instrument'>
                    <div className='photo'>
                        <Fx />
                    </div>
                    <div className='dataInst'>
                        <div className='qty'>4</div>
                        <div className='name'>fx</div>
                    </div>
                </div>
                <div className='instrument'>
                    <div className='photo'>
                        <Air />
                    </div>
                    <div className='dataInst'>
                        <div className='qty'>4</div>
                        <div className='name'>air</div>
                    </div>
                </div>
                <div className='instrument'>
                    <div className='photo'>
                        <Cord />
                    </div>
                    <div className='dataInst'>
                        <div className='qty'>4</div>
                        <div className='name'>cord</div>
                    </div>
                </div>
                <div className='instrument'>
                    <div className='photo'>
                        <Other />
                    </div>
                    <div className='dataInst'>
                        <div className='qty'>4</div>
                        <div className='name'>other</div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Product