import React, { useState, useEffect, useContext } from 'react'
import { useNavigate } from "react-router-dom";

import LogoBlack from '../Assets/img/LogoBlack';
import CartMark from '../Assets/img/CartMark';
import ProfileIcon from '../Assets/img/ProfileIcon';
import ColorLine1 from '../Assets/img/ColorLine1';
import logo from '../Assets/img/test.jpeg';
import TrashCan from '../Assets/img/TrashCan';
import UnloggedAvatar from '../Assets/img/UnloggedAvatar';
import Login from '../components/Login';

import UserContext from "../Store/user-context";

const TopNav = () => {
    const navigate = useNavigate();
    const [isActive, setIsActive] = useState(false);
    const [cartActive, setCartActive] = useState(false);
    const [formSelected, setFormSelected] = useState(false);
    const [hexacolor, setHexacolor] = useState('f44');

    const userCtx = useContext(UserContext);
    

    return (
        <div className='topnav_wrapper'>
            <div className={cartActive ? 'cart active' : 'cart'}>
                <div className='container'>
                    <div className='item'>
                        <div className='border'>
                            <img src={logo} alt="" />
                        </div>
                        <div className='data'>
                            <div className='top'>
                                <div className='title'>pack #14</div>
                                <div className='extraData'>
                                    <div className='author'>Pedro Gonzales</div>
                                    <div className='qtySamples'>32 samples</div>
                                </div>
                                
                            </div>
                            <div className={'price ' + userCtx.color}>$8000</div>
                        </div>
                        <TrashCan color='F44'/>
                    </div>
                    <div className='buyout layout'>
                        <div className={'btn ' + userCtx.color}>ir a pagar</div>
                    </div>
                </div>
            </div>
            <div className={userCtx.userCon ? 'topnav logged active' : 'topnav logged'}>
                <div className='logo' onClick={() => {navigate("/", { replace: true });}}><LogoBlack /></div>
                <div className='extra'>
                    <div className={'btn shop ' + userCtx.color} onClick={() => {navigate("/store", { replace: true });}}>samples</div>
                    <div className='cartBtn' onClick={()=>{setCartActive(!cartActive); setIsActive(false);}}><CartMark color1="000" color2={userCtx.hexacolor}/><p className='qtyCart'>0</p></div>
                    <div className='user' onClick={()=>{setIsActive(!isActive); setCartActive(false);}}><ProfileIcon color={userCtx.hexacolor}/></div>
                </div>
                <div className='square'></div>
                <div className={isActive ? 'menu active' : 'menu'}>
                    <div className='btn profile' onClick={()=>{navigate('/profile', { replace: true })}}>perfil</div>
                    <div className={'btn signout ' + userCtx.color} onClick={userCtx.deConUser}>cerrar sesión</div>
                </div>
                
            </div>
            <div className={userCtx.userCon ? 'topnav unlogged' : 'topnav unlogged active'}>
                <div className='logo' onClick={() => {navigate("/", { replace: true });}}><LogoBlack /></div>
                <div className='extra'>
                    <div className='btn shop red' onClick={() => {navigate("/store", { replace: true });}}>samples</div>
                    <div className='user' onClick={()=>{setIsActive(!isActive); setCartActive(false);}}><UnloggedAvatar color1='f44' color2='fff'/></div>
                </div>
                <div className='square'></div>
                <div className={isActive ? 'menu active' : 'menu'}>
                    <div className='btn login' onClick={()=>{setFormSelected('signin')}}>iniciar sesión</div>
                    <div className='btn signup red' onClick={()=>{setFormSelected('signup')}}>registrarse</div>
                </div>
                
            </div>
            <div className='colorLineTop'>
                <ColorLine1 />
            </div>
            <Login formSelected={formSelected} setFormSelected={setFormSelected}/>
        </div>
    )
}

export default TopNav