import React from 'react'
import Contact from '../components/Contact'
import FAQ from '../components/FAQ'
import Footer from '../components/Footer'
import Main from '../components/Main'
import Prods from '../components/Prods'

const Home = () => {
    return (
        <div className='home'>
            <Main />
            <FAQ />
            <Prods />
            <Contact />
            <Footer />
        </div>
    )
}

export default Home